/* Profiles */
.profile {
    padding-bottom: 30px;

    img {
        display: block;
        height: 200px;
        position: relative;
    }

    a:nth-child(2) {
        font-weight: bold;
        display: block;
        line-height: 2em;
    }

    span {
        font-size: rem-calc(13);
        display: block;
    }
}
