.faculty-spotlight {
    max-width: 360px;
    margin: 0 auto;

    .spotlight-img {
        display: block;
        margin-bottom: 1em;
        margin-top: 1.3em;

        @include breakpoint(xlarge) {
            margin-top: 0;
        }
    }

    .spotlight-excerpt {
        display: block;
        text-align: right;
    }

    .spotlight-name {
        display: block;
        text-align: right;
    }
}
