@font-face {
    font-family: 'waynestate';
    src: url('../fonts/icon/waynestate.eot?70055621');
    src:
        url('../fonts/icon/waynestate.eot?70055621#iefix') format('embedded-opentype'),
        url('../fonts/icon/waynestate.woff?70055621') format('woff'),
        url('../fonts/icon/waynestate.ttf?70055621') format('truetype'),
        url('../fonts/icon/waynestate.svg?70055621#waynestate') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="icon-"]::before, [class*=" icon-"]::before {
    font-family: "waynestate";
    font-style: normal;
    font-weight: normal;
    speak: none;

    display: inline-block;
    text-decoration: inherit;
    width: 1em;
    margin-right: 0.2em;
    text-align: center;

    /* For safety - reset parent styles, that can break glyph codes */
    font-variant: normal;
    text-transform: none;

    /* fix buttons height, for twitter bootstrap */
    line-height: 1em;

    /* Animation center compensation - margins should be symmetric
    remove if not needed */
    margin-left: 0.2em;

    /* Font smoothing. That was taken from TWBS */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

