.menu-site-title {
    background-color: rgba(0, 0, 0, 0.1);
    width: 100%;
    padding: rem-calc(12) 0 rem-calc(12);
    overflow: hidden;

    &.absolute-site-title {
        background-color: rgba(255, 255, 255, 0.7);
        z-index: 1;

        // Site title only absolute for larger views b/c on small the title
        // would take up half the image
        @include breakpoint(large up) {
            position: absolute;
        }
    }

    h1 {
        margin: 0;
        font-size: rem-calc(20);
        text-transform: uppercase;
        line-height: 1em;
        text-shadow: 0 0 1px rgba(255, 255, 255, 0.7);
        word-spacing: 0.1em;

        @media #{$large-up} {
            font-size: rem-calc(26);
        }

        a {
            display: block;
            color: black;
        }

        .site-title-variant {
            font-size: 15px;
            display: block;
            line-height: 1;
        }

        em {
            top: -0.1em;
            font-size: 90%;
            text-transform: none;
            position: relative;
        }

        span {
            display: block;
            font-size: 50%;
            line-height: 1em;
            margin-bottom: 0.3em;
        }
    }
}
