table.table-stack.directory {
    font-size: 0.9em;

    @include breakpoint(xlarge up) {

        // Header
        thead th {
            border-bottom: 1px solid #ccc;
            background: #fff;
            color: #222;
            text-transform: uppercase;
            font-size: inherit;
            padding: 15px 10px 5px 10px;
        }

        .phone {
            width: 20%;
        }
    }
}
