.image-list a {
    display: block;
    text-align: center;

    // Setting a bottom margin per breakpoint
    @each $gutter,$value in $grid-column-gutter {
        @include breakpoint($gutter up) {
            margin: 0 auto map-get($grid-column-gutter, $gutter);
        }
    }

    img {
        display: block;
    }
}
