// Buffer for anchor tags
.content *[id]::before {
    display: block;
    content: " ";
    margin-top: -$menu-top-buffer;
    height: $menu-top-buffer;
    visibility: hidden;
}

.menu-top-container {
    padding: $menu-top-padding;

    &.menu-top-bg {
        background: $menu-top-background;
    }

    // Center vertically
    .vertical-centering {
        display: inline-block;
        height: 100%;
        > * {
            display: table;
            height: 100%;
        }
        > * > *:not(.hide-for-menu-top-up) {
            display: table-cell;
            vertical-align: middle;
        }
    }

    // Remove when Foundation.Sticky is working correctly
    &.sticky {
        width: 100%;
        position: fixed;
        top: 0;
        z-index: 5;
    }

    h1 {
        color: $white;
        display: inline-block;
        font-size: rem-calc(25);
        font-weight: normal;
        line-height: 1em;
        margin: 0;

        a {
            color: $white;
        }

        // Use this class on the <h1> when there is text above or below site title
        // Put the sub-title text in a <span> within the <a> tag
        // <h1><a><span>Sub-title</span>Site title</a></h1>
        &.sub-title {
            font-size: rem-calc(25);
            span {
                display: block;
                font-size: 0.65em;
            }
        }
    }

    ul.menu-top {
        margin: 0;
        // The padding matches the width of the gift image so they never overlap.
        padding: 0 134px 0 0;
        vertical-align: middle;

        li {
            display: inline-block;

            a {
                color: white;
                display: block;
                padding: 0 rem-calc(20);
                font-size: $menu-top-font-size;
                &:hover {
                    color: #ffd478;
                }
                // Remove padding on first link
                // So it lines up on the left
                &:first-child {
                    padding-left: 0;
                }
            }

            &.selected a {
                color: #fc3;
            }
        }

        // Vertically center menu button on small
        &.menu-button {
            line-height: 0;
        }

        .menu-icon {
            // Hide the menu text
            padding: 0;
            span {
                display: none;
            }
        }
    }
}

// Show the main menu based on a custom breakpoint
.show-for-menu-top-up {
    display: none;

    @include breakpoint($menu-top-breakpoint) {
        // Center vertically
        display: table-cell;
    }
}

// Hide the main menu based on a custom breakpoint
.hide-for-menu-top-up {
    // Center vertically
    display: table-cell;

    @include breakpoint($menu-top-breakpoint) {
        display: none;
    }
}
