.alumni-recognition-filters {
    background: #eaeaea;
    padding: 15px 0;
}

.alumni-recognition-item {
    margin-top: 1.4em;
    margin-bottom: 1.4em;

    .name {
        font-weight: bold;
    }

    .title {
        margin: 0.6em 0 0.6em 0;
    }
}

.alumni-recognition-view {
    .department {
        margin: 0.5em 0 2em 0;
        color: #333;
    }

    .return {
        display: block;
        float: right;
    }

    img {
        float: right;
        margin: 1em 0 1em 1em;
    }
}
