.breadcrumbs {
    margin: rem-calc(21) 0 0 0;
    padding: 0;
    font-size: rem-calc(14);

    li {
        display: inline;

        a {
            color: #0c5449;
            text-decoration: none;

            &:hover {
                text-decoration: underline;
                border-bottom: none;
            }
        }

        &:last-child a {
            font-weight: bold;
        }
    }

    .icon-home {
        font-size: 1.2em;
        padding: 0;
        color: #333;
    }

    .text {
        display: none;
    }
}
