.home-hero {
    .feature-image {
        position: relative;
        background-position: top center;
        background-size: cover;
        background-repeat: no-repeat;
        min-height: 200px;
        height: 42vw;
        max-height: 600px;
    }

    .excerpt {
        height: 100%;

        @include breakpoint(xlarge) {
            position: relative;
        }
    }

    .image-info {
        padding: 10px 20px;
        background: rgba(255, 255, 255, 0.65);
        position: absolute;
        width: 100%;
        bottom: 0;

        @media #{$large-up} {
            max-width: 450px;
            bottom: 20px;
            right: 60px;
        }

        @media #{$xlarge-up} {
            bottom: 50px;
            right: 0;
        }

        h4 {
            margin-top: 0;
            margin-bottom: 0.2em;
            font-weight: bold;
        }

        a {
            color: #000;
            text-decoration: underline;
            &:hover {
                text-decoration: none;
            }
        }
    }
}
