@font-face {
    font-family: 'fontello-search';
    src: url('data:application/octet-stream;base64,') format('woff'),
    url('data:application/octet-stream;base64,') format('truetype');
}

.wsuheader {
    background-image: url('data:image/svg+xml;base64,');
    background-image: linear-gradient(#093f39, #0c5449);
    background-size: 100%;
    filter: progid:DXImageTransform.Microsoft.gradient(gradientType=0, startColorstr='#FF093F39', endColorstr='#FF0C5449');
    *zoom: 1;

    padding: 10px 0;
    font: 100% Arial, Helvetica, sans-serif;
    -webkit-font-smoothing: antialiased;
    font-smoothing: antialiased;

    .wsuwordmark {
        text-align: center;

        @include breakpoint(large up) {
            text-align: left;
            margin-top: 15px;
        }

        a {
            white-space: nowrap;
            overflow: hidden;
            height: 35px;
            color: transparent;
            display: block;
        }
    }

    .wsusearch {
        text-align: center;

        @include breakpoint(large up) {
            text-align: right;
        }

        a {
            color: #fff;
            text-decoration: none;
            padding: 2px;
            margin: 0;
            font-size: 0.9em;

            @include breakpoint(small only) {
                font-size: 0.8em;
            }

            @include breakpoint(large up) {
                font-size: 1em;
                padding: 3px;
                margin: 0 1px;
            }

            &:hover {
                background: #006666;
                color: #fff;
                border-radius: 5px;
            }
        }

        .aimhigher a, .aimhigher a:hover {
            color: #fbe090;
            text-transform: uppercase;
        }

        ul {
            list-style: none;
            margin: 0;
            padding: 0 0 5px;
            font-size: 1.1em;

            @include breakpoint(large up) {
                font-size: 1.0em;
            }

            li {
                display: inline-block;
            }
        }

        // This is to make the background color of the button white so it matches the height of the rest of the search box
        .input-group-button {
            background: #fff;
        }

        form {
            margin: 0;
            padding: 0;

            @include breakpoint(large up) {
                margin-bottom: 5px;
            }

            input, button {
                border: none;
                -webkit-box-shadow: none;
                box-shadow: none;
                -webkit-transition: none;
                transition: none;
                margin-bottom: 0;
            }

            button {
                color: #000;
                background-color: #fff;
                text-align: right;
                overflow: hidden;

                // Change these to adjust the height/width of the search button
                font-size: 1.4em;
                padding: .35em .4em;
            }

            label {
                display: none;
            }
        }

        .search-icon {
            font-family: "fontello-search";
            font-style: normal;
            font-weight: normal;
            display: inline-block;
            text-decoration: inherit;
            text-align: center;

            &:before {
                content: '\e800';
            }

            span {
                display: none;
            }
        }
    }
}

.wsufooter {
    background-image: url('data:image/svg+xml;base64,');
    background-size: 100%;
    background-image: linear-gradient(#b1c5c2, #cbdbd9);
    *zoom: 1;
    filter: progid:DXImageTransform.Microsoft.gradient(gradientType=0, startColorstr='#FFB1C5C2', endColorstr='#FFCBDBD9');
    border-top: 1px solid #536461;

    p {
        font-size: 0.8em;
        margin: 0;
        padding: 6px 0;
    }

    a {
        color: #0C5449;
        text-decoration: underline;

        &:hover {
            text-decoration: none;
        }
    }

    .privacy {
        text-align: left;
    }

    .copyright {
        @include breakpoint(large up) {
            text-align: right;
        }
    }
}

@media print {
    .wsuheader, .wsufooter {
        background: transparent;
    }
    .wsuheader {
        .wsuwordmark {
            text-align: left;

            a[href]:after {
                content: ""
            }
        }
    }
}
