.review-form {
    padding: rem-calc(20 0 25 0);
    background-color: #ffe58e;

    h3 {
        margin: 0;
        text-decoration: underline;
        font-size: rem-calc(16);

        @include breakpoint(large up) {
            font-size: 22px;
        }

        a {
            color: black;
            text-decoration: underline;
        }
    }
}
