.profile__block {
    margin-top: 1rem;

    h1.page-title {
        margin-top: 0;
    }

    p.title {
        font-weight: bold;
        font-size: rem-calc(17);
    }

    @include breakpoint(large) {
        margin-top: 1.875rem;
    }

    .profile-info {
        border-bottom: 1px solid #ededed;
        margin-bottom: rem-calc(10);

        @include breakpoint(large) {
            border-bottom: none;
        }
    }

    .profile-data {
        margin-bottom: rem-calc(30);

        a.return-profile {
            position: absolute;
            bottom: 0;

            @include breakpoint(large) {
                float: right;
                position: inherit;
            }
        }
    }
}

.profile__img {
    display: block;
    width: 100%;
    margin-bottom: 1em;
}
